<template>
	<div>
		<swiper :options="swiperOption"  ref="mySwiper" class="swiper">
			<!--  -->
			<swiper-slide v-for="item in items" class="swiper-slide">
				<img :src="item"/>
			</swiper-slide>
			<!--<swiper-slide  class="swiper-slide">
				<img src="../../build/logo.png"/>
			</swiper-slide>
			<swiper-slide  class="swiper-slide">
				<img src="../../build/logo.png"/>
			</swiper-slide>
			<swiper-slide  class="swiper-slide">
				<img src="../../build/logo.png"/>
			</swiper-slide>-->
			<!--  -->
			<div  class="swiper-pagination" slot="pagination"></div>
		</swiper>
		<!---->
		<div class="pic-navs">
			<ul>
				<li>
					<span class="iconfont">&#xe783;</span>
					<span></span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe60f;</span>
					<span></span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe609;</span>
					<span></span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe638;</span>
					<span></span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe65f;</span>
					<span></span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
				<li>
					<span class="iconfont">&#xe7dc;</span>
					<span></span>
					<span><img src="../../static/img/fif_list_but_jiantou.png"/></span>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	export default {
		components:{
			swiper,
			swiperSlide
		},
		data() {
			return {
				items:["static/img/lb1.png","static/img/lb1.png","static/img/lb1.png"],
				swiperOption: {
				observer:true,//swiperswiper 
				observeParents:true,//swiperswiper 
				//notNextTicktrueNextTickswiperswiperswipertrue
				notNextTick: true,
		        pagination:{
		        	el:'.swiper-pagination',
		        },
		        autoplay:{
		        	delay:1000,
		        	disableOnInteraction:false
		        },
		        slidesPerView: 'auto',
		        centeredSlides: true,
		        paginationClickable: true,
		        spaceBetween: 30,
		            onSlideChangeEnd: swiper => {
		            	//swiper
		            	this.page = swiper.realIndex+1;
		            	this.index = swiper.realIndex;
		            }
		        },
		        //
		        navOption: {
		        slidesPerView:4,
				notNextTick: true,
		        pagination:{
		        	el:'.swiper-pagination',
		        },
		        slidesPerView: 'auto',
		        centeredSlides: true,
		        paginationClickable: true,
		        spaceBetween: 30,
		            onSlideChangeEnd: swiper => {
		            	//swiper
		            	this.page = swiper.realIndex+1;
		            	this.index = swiper.realIndex;
		            }
		        }
			}
			
		},
		//sweiper
		computed: {
		    swiper() {
		      return this.$refs.mySwiper.swiper;
		    }
		},
		mounted () {
			//swiperswiper
		    this.swiper.slideTo(0, 0, false);
		}
 
	}
</script>
<style scoped>
	*{
		padding: 0;
		margin: 0;
	}
	.swiper{
		width: 100%;
		height: 600px;
	}
	.swiper-slide{
		width: 100%;
		height: 600px;
	}
	.swiper-slide img{
		width: 100%;
		height: 600px;
	}
	.swiper-pagination-bullet{
		width: 20px !important;
		height: 20px !important;
	}
	/**/
	.pic-navs{
		width: 100%;
		height: 730px;
		border: 1px solid redd;
	}
	.pic-navs ul{
		width: 100%;
		height: 730px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	.pic-navs ul li{
		width: 525px;
		height: 200px;
		background: #eeeeee;
		display: flex;
		justify-content: space-around;
		align-items: center;
		margin-top: 30px;
	}
	.pic-navs ul li span:nth-child(1){
		font-size: 100px;
	}
	.pic-navs ul li span:nth-child(2){
		font-size: 50px;
	}
	.pic-navs ul li span:nth-child(3){
		font-size: 100px;
	}
</style>


